import Cookies from "js-cookie"

class Song
{
    constructor(song_data)
    {
        this.audio = new Audio()
        this.songArray = []
        this.i = -1;
    }
    play()
    {
        console.log("playing")
        this.audio.play()
        this.i = this.songArray.indexOf(this.song)
        this.audio.addEventListener("timeupdate", (e)=>{
            if(this.audio.currentTime == this.audio.duration){
                if(this.i > -1 && this.i<this.songArray.length){
                    console.log(this.i)
                    const nextSong = this.getNextSong()
                    if(nextSong){
                        this.playSong(nextSong)
                    }
                }
            }
                
        })
        if(this.i == -1){
            this.songArray = []
            this.songArray.push(this.song)
            this.i ++
        }
        console.log(this.songArray)
        let current_final;
        let current_inital;
        let callApiWithDelay;
        let areViewsUpdated = false;

        this.audio.addEventListener("play", (e)=>{
            current_inital = this.audio.currentTime
            if(!areViewsUpdated) {
            callApiWithDelay = setTimeout(() => {
                this.updateViews(current_inital, this.audio.currentTime)
                areViewsUpdated = true;
            }, 30000 - this.audio.currentTime)
            }
        })
        this.audio.addEventListener("pause", (e)=>{
            current_final = this.audio.currentTime
            this.updateViews(current_inital, current_final)
            clearTimeout(callApiWithDelay)
        })
        this.audio.addEventListener("ended", (e)=>{
            current_final = this.audio.currentTime
            this.updateViews(current_inital, current_final)
            clearTimeout(callApiWithDelay)
        })
        
       
    }
    
    stop()
    {
        this.audio.pause()
    }

    loadSongs(song, playlist_song = "song")
    {
        if(playlist_song === "playlist"){
            this.songArray = song
        }else if(playlist_song === "song"){
            this.songArray.push(song)
        }
    }

    removeSongs(song)
    {
        const newArray = this.songArray.filter(arr=>arr!=song)
        this.songArray = newArray
    }

    getNextSong()
    {
        if(this.songArray && this.i > -1 && this.i<this.songArray.length){
            const nextSong = this.songArray[this.i+1]
            //this.playSong(nextSong)
            return nextSong
        }else{
            return null
        }
    }

    getPreviousSong()
    {
        if(this.songArray && this.i > 0 && this.i<this.songArray.length){
            const nextSong = this.songArray[this.i-1]
            console.log(nextSong)
            //this.playSong(nextSong)
            return nextSong
        }else{
            return null
        }
    }

 

    playSong(song)
    {
        this.song = song
        this.audio.src = song.src.aduioURL
        this.play(song)
    }

    like(liker_uid)
    {
        const url = "/api/music/like/send"
        const data = 
        {
            likedBy: liker_uid,
            likedOf: this.song.info.uid,
            song_name: this.song.info.songID || this.song.info.title,
            song: this.song
        }
        this.postData(url, data)
    }

    updateViews(initialTime, finalTime)
    {
        const url = "/api/music/views/update"
        const data = 
        {
            info:{
                initialTime: initialTime,
                finalTime: finalTime,
            },
            track: this.song
        }
        this.postData(url, data)
    }

    createPlaylists(playlist_obj)
    {
        this.songArray = []
        this.loadSongs(playlist_obj.songs, "playlist")
    }

    volumeHandler(val)
    {
        this.audio.volume = val
    }
    getCurrentSong()
    {
        return this.song
    }
    getDuration()
    {
        if(this.audio.duration){
            const rawDuration_minute = Math.floor(this.audio.duration/60) 
            const duration_minute = rawDuration_minute.toLocaleString('en-US', {
                minimumIntegerDigits: 2,
                useGrouping: false
            })
            const rawDuration_sec = Math.floor(this.audio.duration%60) 
            const duration_sec = rawDuration_sec.toLocaleString('en-US', {
                minimumIntegerDigits: 2,
                useGrouping: false
            })
            return duration_minute + ":" + duration_sec
        }else{
            return "00:00"
        }
    }
    getCurrentTime(){
        if(this.audio.currentTime){
                const rawCurrent_minute = Math.floor(this.audio.currentTime/60) 
                const current_minute = rawCurrent_minute.toLocaleString('en-US', {
                    minimumIntegerDigits: 2,
                    useGrouping: false
                })
                const rawCurrent_sec = Math.floor(this.audio.currentTime%60) 
                const current_sec = rawCurrent_sec.toLocaleString('en-US', {
                    minimumIntegerDigits: 2,
                    useGrouping: false
                })
                return current_minute + ":" + current_sec 
        }else{
                return "00:00"
        }
    }
    isPaused()
    {
        return this.audio.paused
    }
    playlistIncludes(song)
    {
        return this.songArray.includes(song)
    }
    showPlaylists()
    {
        return this.songArray
    }

    isQued(song)
    {
        let result = false
        if(this.songArray){
            for(let i = 0; i<this.songArray.length; i++){
                if(song.info.songID == this.songArray[i].info.songID){
                    result = true;
                    break;
                }else{
                    result = false
                }
            }
        }
        return result
    }

    isPlaying(song)
    {
        if(!this.audio.paused && song.src.aduioURL === this.audio.src)
        {
            return true
        }else if(this.audio.src !== song.src.aduioURL || this.audio.paused){
            return false
        }
    }


    async  postData(url = '', data = {}) {
        // Default options are marked with *
        const response = await fetch(url, {
          method: 'POST', // *GET, POST, PUT, DELETE, etc.
          mode: 'cors', // no-cors, *cors, same-origin
          cache: 'no-cache', // *default, no-cache, reload, force-cache, only-if-cached
          credentials: 'include', // include, *same-origin, omit
          headers: {
            "CSRF-Token": Cookies.get("XSRF-TOKEN"),
            'Content-Type': 'application/json',
          },
          redirect: 'follow', // manual, *follow, error
          referrerPolicy: 'no-referrer', // no-referrer, *no-referrer-when-downgrade, origin, origin-when-cross-origin, same-origin, strict-origin, strict-origin-when-cross-origin, unsafe-url
          body: JSON.stringify(data) // body data type must match "Content-Type" header
        });
        return response.json(); // parses JSON response into native JavaScript objects
    }    
}

export {Song}





class Playlist
{
    constructor(name, song_array)
    {
        this.songs = song_array||[]
        this.playlistName = name
    }
    createNewPlaylist(name, song_array)
    {
        this.playlistName = name
        this.songs = song_array
    }
    addSong(song)
    {
        this.songs.push(song)
    }
    removeSong(song)
    {
        const newPlaylist = this.songs.filter(item=>item !== song)
        this.songs = newPlaylist
    }
    getSongs()
    {
        return this.songs
    }
}

export {Playlist}