import React, { Fragment, useEffect, useRef,  } from "react";
import firebase from "firebase";
import { useLocation } from 'react-router-dom';
import { useState } from "react"
import MusicPreview from "./widgets/musicPreview"
import Moosix from "./functions/mossix-api"
import Loader from "react-loader-spinner"
import {Helmet} from "react-helmet";
const app = new Moosix()
function Result(props){
    const [currentUserName, setCurrrentUserName] = useState()
    
    const [musics, setMusics] = useState([])
    const [isLoading, setIsLoadinig] = useState(true)
    
 
    let query = useQuery();
    function useQuery() {
        return new URLSearchParams(useLocation().search);
    }
    const search_query = query.get("search")
    
    console.log(search_query)  

    useEffect(()=>{
        if(search_query){
            setMusics([])
            setIsLoadinig(true)
            app.searchQ(search_query).then((musics)=>setMusics(musics)).finally(()=>setIsLoadinig(false))
        }else{
        
        }
        
    }, [search_query])
    
    function play(name){
        props.play(name)
    }

    function stop(){
        props.stop()
    }

    

    return(
        <>
        <Helmet>
            <title>{search_query + " • Moosax"}</title>
        </Helmet>
        <div id = "home" className ="main">
            <div className = "music-content-container" id = "home-musics">
                <div className = "music-box">
                    <div className = "user-musics">
                        {musics && musics.map((name, i)=>{   
                            return(
                                <Fragment key = {i}>
                                    <MusicPreview 
                                        name = {name} 
                                        play = {play}
                                        stop = {stop} 
                                        uID = {name.userID} 
                                        pfp= {name.pfpURL} 
                                        user = {name.artist}
                                        audio = {props.audio}
                                    />
                                    </Fragment>
                                )
                        })}
                        { isLoading
                        ?
                            <div className = "loader">
                                <Loader type = "TailSpin" color = "#fff"/>
                            </div>   
                        :
                            ()=>{
                                if(musics.length == 0){
                                    return <span>No Results</span>
                                }
                            }
                            
                           
                        }
                    </div>    
                </div>
            </div>  
        </div>
        
        </>
    )
}
export default Result