import React, { useEffect, useRef } from "react";

import {songContext} from "../../context/songContext"
import { useState, useContext } from "react"
import { NavLink } from "react-router-dom";
import Like from "./like"
import Hover1 from "./Hover1.mp3"
import {MoosaxAuth} from "../functions/moosax-auth.js"
import ThreeDots from "../threedots"
import Moosix from "../functions/mossix-api.js";
function MusicPreview(props){
    const app = new Moosix()
    const auth = new MoosaxAuth()
    const [btn_hidden, setBtn_hidden] = useState(true)
    const [show, setShow] = useState(true)
    const song = useContext(songContext)
    const name = props.name;
    const [currentUserName, setCurrrentUserName] = useState() 
    // useEffect(()=>{
    //     auth.getUserId((id)=>app.getUserData(id).then(data=>setCurrrentUserName(data.public.profile.info.name)))
    // }, [])
    useEffect(()=>{
        if(song.audio.src != name.src.aduioURL || song.audio.paused){
            setBtn_hidden(true)
        }
       
        if(song.audio.src == name.src.aduioURL && !song.audio.paused){
            setBtn_hidden(false)//false == not played
        }
    }, [song.audio.src, name.src.aduioURL, song.audio.paused])

    function play(event){
        setBtn_hidden(false)
        props.play(name)
    }
    function stop(event){
        setBtn_hidden(true)
        props.stop(event)
    }
    
    return(
         <div className = "music-preview-box" onMouseOut = {()=>setShow(false)}>
            
            <NavLink to = {`/listen?m=${name.info.songID}`}>
                <div className = "user-musics-content" style ={ { backgroundImage: `url("${name.src.imageURL}")` } } />
            </NavLink>
           <div  className = "-button">
                    {btn_hidden?
                    <i className="material-icons"onClick = {play} value = {name.info.title} id = "play_hover">
                        play_arrow
                    </i>:
                    <i onClick = {stop} className = "material-icons">pause</i>
                    }
                    
            </div>
            <ThreeDots song = {name} show = {show}/>  
            <div id = "user-song-info">
            <div class = "user-song-title">
                    <span>{name.info.artist} - {name.info.title}</span>
            </div> 
                <div class = "user-song-user">
                    <div class = "mini-pfp">
                        <img src = {props.pfp} />
                    </div>
                    <NavLink to ={"/u/"+name.info.userID+"/music"}><span>{name.uploader.info.name}</span></NavLink>
                </div>

                <div className = "stats">
                    <div class = "views">
                        <i class="material-icons">
                            play_arrow
                        </i>
                        <span>{name.stats.views}</span>
                    </div>
                    <Like name = {name.info.title} uID = {name.info.userID} userName = {currentUserName} music = {name}/>
                </div>
                
            </div>
    </div>
    )
}

export default MusicPreview
