import { NavLink } from "react-router-dom";
import React from "react";
import firebase from "firebase/app"
import { useHistory } from "react-router-dom"

import "./firebase"
import {MoosaxAuth} from "./functions/moosax-auth.js"
import { useState } from "react"

function Login(props){
    const history = useHistory();
    const auth = new MoosaxAuth()
    let email;
    let password;
    let userName;  
    const [errorMessage, setErrorMessage] = useState()
    
    auth.isloggedIn().then((res)=>{
        if(res){
            history.push("/")
        }
            
        
    
    })

    function login(props){
        console.log("hey")
        email = document.getElementById("email").value;//gets type value of user email
        password = document.getElementById("password").value;//gets type value of user password
        auth.login(email, password)
        .then((err)=>{
            
            if(err){
                setErrorMessage(err)
            }else{
                console.log(err)
                history.push("/explore")
            }
        })
    }
    
    return(
        
        <div id = "login" className ="main-login">
              
            <div className = "form-box">
                <div className = "form-container">
                    <div className = "inputHolder">
                        <div className = "logo-form">
                            <h1>Logo</h1>
                        </div>
                        <div className = "input-container">
                            
                            <div className = "email-holder">
                                <input placeholder = "email adress" id = "email"
                                className = "-input" />
                            </div>
                            <div className = "password-holder">
                                <input placeholder = "password"  id = "password" type = "password" className = "-input"/>
                            </div>
                            <div className = "btn-holder">
                                <button onClick = {login}><span><b>Login</b></span></button>
                            </div>
                        </div>
                        
                    </div>
                    <div class = "link-signup">
                        <p>{errorMessage}</p>
                        <p>Havent created account? plz <NavLink to = "./signup"> click here</NavLink></p>   
                    </div>
                </div>
            </div>
           
        </div>
        
    )
}
export default Login;