import React from "react"
import { useHistory } from "react-router-dom"
import firebase from "firebase/app"
import "./firebase"
import Moosix from "./functions/mossix-api"

import { NavLink } from "react-router-dom";
function Signup(){
    const app = new Moosix()
    
    const history = useHistory()
    function signUp(){
        let uEmail = document.getElementById("email").value;
        let uPassword = document.getElementById("password").value;
        let userName = document.getElementById("userName").value;
        async function send(email, password){
            await firebase.auth().createUserWithEmailAndPassword(email, password)
            const userID = firebase.auth().currentUser.uid;
            const data = {
                uid: userID,
                private:{
                    "userName": userName,
                    "userId": userID,
                },
                public:{
                    profile:{
                        "info": {
                            "bio": "",
                            "firstName": userName,
                            "lastName": userName,
                            "name": userName,
                            "songs": 0,
                            "followers": 0,
                            "following": 0 
                        },
                        "pfp":{
                            "url":"",
                            "x": 50,
                            "y": 50,
                            "zoom": 1
                        }
                    }
                }
            }
            app.createAcc(data)
            firebase.database().ref("users/"+userID+"/private/login credentials").set({
                "userName": userName,
                "userId": userID,
                "userLog": true
            })
            firebase.database().ref("users/"+userID+"/public/profile").set({
            "info": {
                "bio": "",
                "firstName": userName,
                "lastName": userName,
                "name": userName
            },
            "pfp":{
                "url":"",
                "x": 50,
                "y": 50,
                "zoom": 1
            }
            })
            history.push("./login")
        }
        send(uEmail, uPassword);
    }
    return(
        <div id = "signUp" className = "main-login">
            <div className = "form-box">
                <div className = "form-container">
                    <div class = "logo-form">
                        <h1>Logo</h1>
                    </div>
                    <div className = "input-container">
                        <div className = "email-holder">
                            <input id = "email" placeholder = "email" className = "-input"/>
                        </div>
                        <div className = "password-holder">
                            <input id = "password" placeholder = "password" className= "-input" type = "password"/>
                        </div>
                        <div className = "username-holder">
                            <input id = "userName" placeholder = "username" className = "-input"/>
                        </div>
                        
                        <div class = "btn-holder">
                            <button onClick = {signUp}>Next</button>
                        </div>
                        
                    </div>
                    <div class="link-signup">
                        <p>Already have an account? plz <NavLink to = "./login"> click here</NavLink></p>   
                    </div>
                </div>
                
            </div>
               
        </div>
    )
}
export default Signup