import React, { useContext, useEffect, useState } from "react";
import {uidContext} from "../../context/uid"
import "./styles/style.css"
import Moosix from "../functions/mossix-api.js"
import Top from "./comp/top"
import {songContext} from "../../context/songContext.js"
import {Playlist} from "../classes/song"
import {MoosaxAuth} from "../functions/moosax-auth.js"
import PlaylistContainer from "./comp/playlistContainer"
import {Helmet} from "react-helmet";
const app = new Moosix()

function UserLikes(props)
{
    const song = useContext(songContext)
    const auth = new MoosaxAuth()
    const [musics, setMusics] = useState(null)
    const [tracks, setTracks] = useState(null)
    const [isLoading, setIsLoadinig] = useState(true) 
    useEffect(()=>{
        app.getUserLikes().then(data=>{
            console.log(data)
            setMusics(data)
        }).finally(()=>{
            setIsLoadinig(false)
        })

    }, [])
        
        

    useEffect(()=>{
        if(musics){
            const playlist = new Playlist()
            playlist.createNewPlaylist("Likes", musics)
            song.createPlaylists(playlist)
            console.log(song.showPlaylists())
            setTracks(song.showPlaylists())
        }
    }, [musics])
    return(
        <div id = "userLikes" className = "main" >
            <Helmet>
                <title>Liked Songs • Moosax</title>
            </Helmet>
            <div className = "music-content-container" id = "userLikes-music">
                <div className = "music-box" id = "userLikes-box">
                    <Top tracks = {tracks} nowPlaying = {null} play ={props.play} audio = {props.audio} stop = {props.stop}/>
                    <PlaylistContainer tracks = {tracks} isLoading = {isLoading}/>
                </div>
            </div>
        </div>
    )
}




export default UserLikes