import React from "react"
import { useState, useEffect, useContext } from "react"
import AudioPreview from "./audioComp/audioPreview"
import {songContext} from "../context/songContext"

function AudioPlayer(props){
    const song = useContext(songContext)
    const [progVal, setProgVal] = useState(0)
    const [volume, setVolume]  = useState(1)
    const [currentTime, setCurrentTime] = useState(song.getCurrentTime())
    
    // useEffect(() => {
    // //    setProgVal(props.progVal)
    //   },[props.progVal])
    useEffect(()=>{
        song.audio.addEventListener("timeupdate", (e)=>{
            const duration = (song.audio.currentTime/song.audio.duration)*100
            setCurrentTime(song.getCurrentTime())
            setProgVal(duration)
        })
    }, [props.AudioInfo])
    

    function stop(e){
        props.stop()
    }

    function play(){
        props.playAudio()
    }

    function seek(e)
    {
        e.preventDefault()
        const target = e.target || e.srcElement,
        rect = target.getBoundingClientRect(),
        offsetX = e.clientX - rect.left
        const percent =offsetX / document.documentElement.offsetWidth;
        console.log(percent)
        song.audio.currentTime = percent * song.audio.duration * 1.9;
    }

    function done(e){
        e.preventDefault()
        const target = e.target || e.srcElement,
        rect = target.getBoundingClientRect(),
        offsetX = e.clientX - rect.left
        const percent =offsetX / document.documentElement.offsetWidth;
        console.log(percent)
        song.audio.currentTime = percent * song.audio.duration * 1.9;
    }


   

    useEffect(()=>{
        song.volumeHandler(volume)
    }, [volume])
    

    function next(){
        const nextSong = song.getNextSong()
        if(nextSong)
        {
          song.playSong(nextSong)
        }
      }
      
      function previous(){
        const previousSong = song.getPreviousSong()
        if(previousSong)
        {
          song.playSong(previousSong)
        }
      }
    

    
    return(
            <div className = "audio-wrapper">
                <div className = "audio-container">
                    <div className = "audio-box">
                        <AudioPreview audio = {props.AudioInfo}/>
                        <div className = "audio_elements-container">
                            <div className = "play_btn-container">
                                <div className = "play-container">
                                    {
                                        song.audio.paused? 
                                        <span onClick = {play} id = "play_btn" className = "material-icons">play_circle_outline</span>
                                        :
                                        <span onClick = {stop} id = "play_btn" className = "material-icons">pause_circle_outline</span>
                                    }
                                </div>
                              
                                <span className="material-icons" id = "fast_rewind"  onClick = {previous}>
                                    skip_previous
                                </span>
                                <span className="material-icons" id = "fast_forward" onClick = {next}>
                                    skip_next
                                </span>
                                
                            </div>
                            <div className = "progress_div">
                                <div class = "progress_holder" id= "prog_holder"onMouseDown = {seek} onMouseUp ={done} >
                                    <div id = "progress_meter" style = {{width: progVal + "%"}} ></div>
                                </div>  
                            </div>
                            <div className = "current">
                                <span>{currentTime}</span>
                            </div>
                            <div className = "duration">
                                <span>{song.getDuration()}</span>
                            </div>
                            <div className = "volume">
                                <div className = "volume-i">
                                    <span class="material-icons">
                                        volume_up
                                    </span>
                                </div>
                                <div className = "volume_slider">
                                    <input type = "range" onChange = {(event)=>setVolume(event.target.value/100)} value = {volume*100}/>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
    )
}

export default AudioPlayer