import firebase from "firebase"
import Cookies from "js-cookie"
class MoosaxAuth
{
    async login(email, password)
    {
        console.log("hey login")
        firebase.auth().setPersistence(firebase.auth.Auth.Persistence.NONE)
        try{
            // await firebase.auth().signInWithEmailAndPassword(email, password)
            // .then(({user})=>{
            //     return user.getIdToken().then(idToken=>{
            //         if(idToken){
            //             this.postData("/api/login", {idToken}).then(()=>{
            //                 return {status: "complete"}
            //             })
            //         }
                    
            //     })
            // })
            const signIn = await firebase.auth().signInWithEmailAndPassword(email, password)
            const user = signIn.user
            const token = await user.getIdToken()
            await this.postData("/api/login", token)
            return "ok"
        }
        catch(err){
            return err.message
        }
        
    }

    async signOut()
    {
        const res = await fetch("/signout", {
                            method: 'get',
                            credentials: 'include' 
        })
        const response = await res.text()
        return response
    }
    async isloggedIn()
    {
        const res = await fetch("/isLoggedIn")
        const response = await res.json()
        const status = response.signedout
        return !status;
    }
    async getUserId()
    {
        const res = await fetch("/api/userId")
        const response = await res.json()
        const uid = response.uid;
        return uid;
    }
    async  postData(url = '', data) {
        // Default options are marked with *
        const response = await fetch(url, {
          method: 'POST', // *GET, POST, PUT, DELETE, etc.
          mode: 'cors', // no-cors, *cors, same-origin
          cache: 'no-cache', // *default, no-cache, reload, force-cache, only-if-cached
          credentials: 'same-origin', // include, *same-origin, omit
          headers: {
            'Content-Type': 'application/json',
            "CSRF-Token": Cookies.get("XSRF-TOKEN")
          },
          redirect: 'follow', // manual, *follow, error
          referrerPolicy: 'no-referrer', // no-referrer, *no-referrer-when-downgrade, origin, origin-when-cross-origin, same-origin, strict-origin, strict-origin-when-cross-origin, unsafe-url
          body: JSON.stringify({data}) // body data type must match "Content-Type" header
        });
        return response.json(); // parses JSON response into native JavaScript objects
    }
}

export {MoosaxAuth}