
import React, { useEffect, useState, useContext } from "react";
import Moosix from "../../functions/mossix-api.js"
import {songContext} from "../../../context/songContext"
const app = new Moosix()

function Top(props)
{
    const [track, setTrack] = useState()
    const [title, setTitle] = useState()
    const [image, setImage] = useState()
    const [artist, setArtist] = useState()
    const [isSameSrc, setIsSameSrc] = useState(false)
    const [description, setDescription] = useState()
    const song = useContext(songContext)
    useEffect(()=>{
        
        if(props.nowPlaying){
           
        }
        else if(Array.isArray(props.tracks)){
            if(song.playlistIncludes(song.getCurrentSong())){
                setTrack(song.getCurrentSong())
            }else{
               setTrack(props.tracks[0])
            }
          
        }
        else{
            setTrack(props.tracks)
        }
    }, [props])

    function play()
    {
        song.playSong(track)
        setIsSameSrc(true)
    }

    function stop()
    {
        song.stop()
        setIsSameSrc(false)
    }

    useEffect(()=>{
        if(track){
            setTitle(track.info.title)
            setImage(track.src.imageURL)
            setArtist(track.info.artist)
            setDescription(track.info.description)    
            if(song.audio.src == track.src.aduioURL && song.audio.paused == false ){
                setIsSameSrc(true)
             }else{
                setIsSameSrc(false)
             }
        }
    }, [track, song.audio?.src,  track?.src.aduioURL,  song.audio.paused])
   
    return (
        <div className = {props.top||"top"} >
            <div className = "top-container">
                <div className = "bg-blur" style = {{backgroundImage: `linear-gradient(to bottom, rgba(0, 0, 0, 0), var(--dark-secondary)), url("${image}")`}} />
                <div className = "top-contents">
                    <div className = "top-albumCover-wrapper">
                        <div className = "top-albumCover" 
                        style = {{backgroundImage: `url("${image}")`}}
                        />
                    </div>
                    <div className = "top-info">
                        <div className = "top-title">
                            <h2>{title}</h2>
                        </div>
                        <div className = "top-description">
                            <span>{description}</span>
                        </div>
                        <div className = "top-artist-name">
                            <span>{artist}</span>
                        </div>
                    </div>
                    <div className = "top-buttons">
                        <div className = "top-play-botton">
                            {isSameSrc?<button onClick = {stop}>pause</button>:<button onClick = {play}>play</button>}
                            
                        </div>
                        <div className = "top-more-button">

                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Top