import React, { useEffect } from "react"
import {songContext} from "../../context/songContext"
import { useState, useContext } from "react"
function AudioPreview()
{
    const song = useContext(songContext)
    const [track, setTrack] = useState()
    useEffect(()=>{
        setTrack(song.getCurrentSong())
    }, [song.getCurrentSong()])
  
    return(
        <div id = "audio-preview">
            <div className = "audio-preview-container">
                <div className = "audio-preview-box">
                    <div className = "audio-preview-contents">
                        <div className = "audio-preview-image">
                            <div className = "image" 
                            style=
                            {
                                {
                                    backgroundImage: `url("${track?track.src.imageURL:null}")`
                                }
                            }/>
                        </div>
                   
                       
                      
                        <div className = "audio-preview-context">
                            <div className = "audio-preview-title">
                                <span>{track?track.info.title:"unknown"}</span>
                            </div>
                            <div className = "audio-preview-artist">
                                <span>{track?track.info.artist:"unknown"}</span>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default AudioPreview