import React, { useContext, useEffect } from "react";

import firebase from "firebase";
import Popular from "./sub-home/Popular"
import Notification from "./functions/notification.js"
import { useState } from "react";
import Recomend from "./sub-home/Recomend"
import Trending from "./sub-home/Trending"
import {uidContext} from "../context/uid"
import RecomendFollowing from "./sub-home/RecommendFollowing"





function Home(props) {
    let name1;
   
    document.title = "Moosix"
    const uid = useContext(uidContext)
    // const socket =  Notification(uid)
    

    // if(socket){
    //     socket.on("notification", data=>console.log(data))
    // }
    const db = firebase.database()
    const [currentUserName, setCurrrentUserName] = useState();
    const [progVal, setProgVal] = useState("0%");
    const auth = firebase.auth()
    

    useEffect(()=>{
        if(uid){
           
            firebase
            .database()
            .ref("users/" +uid + "/private/login credentials")
            .on("value", (snapshot) => {
                console.log(snapshot)
                setCurrrentUserName(snapshot.val().userName);
            });                                                    
       }
    }, [uid])
    

       
    
    

  

 
    let duration = 0;
    function play(src) {
        props.play(src)
    }


    function stop(event) {
       props.stop()
    }

 
    return (
        <>
            <div id="home" className="main">
            <div className="music-box" id = "home-box">
                <div className="music-content-container" id="home-musics">
                        <Popular play = {play} stop = {stop} userName = {currentUserName} audio = {props.audio}/>
                        <Trending play = {play} stop = {stop} userName = {currentUserName} audio = {props.audio}/>
                        <Recomend  play = {play} stop = {stop} userName = {currentUserName} audio = {props.audio}/>
                        <RecomendFollowing play = {play} stop = {stop} userName = {currentUserName} audio = {props.audio} />
                    </div>
                </div>
            </div>
        </>
    );
}
export default Home;
