import React, {useEffect, useState} from "react"
import "./styles/followBtn.css"
import Moosix from "../functions/mossix-api"

function FollowBtn({uid, following})
{
    const [isFollowing, setIsFollowing] = useState()
    const app = new Moosix;
    useEffect(()=>{
        setIsFollowing(following)
    }, [following])
    return(
        <div id = "follow-btn">
            <div className = {isFollowing?"follow-btn-box-active":"follow-btn-box-inactive"} id = "follow-btn-box">
                <div className = "follow-btn-container">
                    <div className = "follow-btn-letter" onClick = {()=>{
                        app.follow(uid)
                        setIsFollowing(!isFollowing)
                        }}>
                        {
                            isFollowing?<span>Following</span>:<span>Follow</span>
                        }
                    </div>
                    <div className = "follow-btn-icon">
                        {
                            isFollowing?<span class="material-icons">done</span>:<span class="material-icons">add</span>
                        }
                        
                        
                    </div>
                </div>
            </div>
        </div>
    )
}

export default FollowBtn