import React, {useState, useEffect, useContext} from "react"
import Moosix from "../functions/mossix-api.js"
import {uidContext} from "../../context/uid.js"

const app = new Moosix()
function NotficationContents(props)
{
    // const [notification, setNotification]
    const notfication = props.notification
    const [didWhat, setDidWhat] = useState()
    const [x, setX] = useState()
    const [y, setY] = useState()
    const [zoom, setZoom] = useState()
    const [user_data, setUser_data] = useState()
    const [userName, setUserName] = useState()
    const [activity, setActivity] = useState()
    const [pfp, setPfp] = useState(null)
    useEffect(()=>{
        if(notfication){
            setUser_data(notfication.user)
            if(notfication.type == "like"){
                setDidWhat("liked your")
                setActivity("song " + notfication.title)
            }
            else if(notfication.type == "comment"){
                setDidWhat("commented")
            }
            else if(notfication.type == "followed"){
                setDidWhat("followed you")
            }

        } 
    },[notfication])
  
   

    useEffect(()=>{
        if(user_data){
            setUserName(user_data.info.name)
            setPfp(user_data.pfp.url)
            setX(user_data.pfp.x)
            setY(user_data.pfp.y)
            setZoom(user_data.pfp.zoom)
        }
    }, [user_data])
    
  
    
    return(
        <div className = "notification-items">
            <div className = "notification-profile">
                 <div className="pfp" id = "notfication-pfp">
                        <div className = "pfp-container" id = "notfication-pfp-container">
                            <pfp className = "pfp-box" id = "notfication-pfp-box">
                                <img id= "notfication-pfp-img" 
                                src={pfp}  
                                style = 
                                {
                                    {
                                        transform: `translate(${x-50}px, ${y-50}px) scale(${(parseInt(zoom)/100) + 1 })`,
                                        
                                    }
                                }
                                />
                            </pfp>
                        </div>
                    </div>
            </div>
            <div className = "notification-info">
                <div className = "notification-userName">
                    {userName}
                </div>

                <div className = "notfication-type">
                    <span>{didWhat}</span>
                </div>

                <div className = "notification-activity">
                    <span>{activity}</span>
                </div>               
                
            </div>
           
        </div>
    )
}

export default NotficationContents