import React from "react";
import { NavLink } from "react-router-dom";


function NavDiv1()
{
    return(
            <div className = "nav-div1">
                <span className = "nav-heading">Navigation</span>
                <ul>
                    <NavLink to="/" exact activeClassName="btn-active">
                        <li className="btn">
                            <span class="material-icons">
                                home
                            </span>
                            <p>Home</p>
                        </li>
                    </NavLink>
                    <NavLink to="/explore">
                        <li className="btn" >
                            <span class="material-icons">
                                explore
                            </span>
                            <p>explore</p>
                        </li>
                    </NavLink>
                    <NavLink to="/upload" exact activeClassName="btn-active">
                        <li className="btn" >
                            <span class="material-icons md-dark md-inactive">
                                upload
                            </span>
                            <p>upload</p>
                        </li>
                    </NavLink>
                    <NavLink to="/account/edit" exact activeClassName="btn-active">
                        <li className="btn" id="ab">
                            <span class="material-icons">
                                perm_identity
                            </span>
                            <p>edit</p>
                        </li>
                    </NavLink>
                   
                </ul>
            </div>
    )
}

export default NavDiv1