import React, { Fragment,  } from "react";
import MusicPreview from "./widgets/musicPreview"
import MusicCard from "./musicCard2/musicCard2"
import { useLocation } from 'react-router-dom'
function MusicContent(props){
    let musics = props.musics;
    let uID = props.uID;
    let currentUserName = props.userName
    let pfp = props.pfp
    let user = props.user
    function play(src){
        props.play(src)
    }
    function stop(){
        console.log("yes stop")
        props.stop()
    }

    const query = useQuery();
    function useQuery() {
        return new URLSearchParams(useLocation().search);
    }
    const view_mode = query.get("view")
   
    return(
        <div className = "music-box" id = "music-box-userPage">
                <div className = "music-content-container">
                        <div className = "user-musics">
                                {musics && musics.map((name, i)=>{   
                                    return(
                                        <Fragment key = {i}>
                                            {
                                                view_mode === "list"?<MusicCard track = {name} />
                                                :
                                                <MusicPreview 
                                                name = {name} 
                                                play = {play} 
                                                uID = {uID} 
                                                userName = {currentUserName} 
                                                pfp= {pfp} 
                                                user = {user}
                                                stop = {stop}
                                                audio = {props.audio}
                                            />
                                        
                                            }
                                               
                                                
                                        </Fragment>
                                        
                                    )
                                })}
                        </div>
                    </div>
                </div>  
    )
}

export default MusicContent