import React, { useContext } from "react";
import "./style.css"
import {songContext} from "../../context/songContext"
import ThreeDots from "../threedots"
function MusicCard(props){
    const song = useContext(songContext)
    const track = props.track
    return(
        <div id = "music-card">
            <div className = "music-card-box">
                <div className = "music-card-container">
                    <div id = "music-card-cover">
                        
                        <div className = "album-cover">
                            <span
                            style = {{ backgroundImage: `url("${track.src.imageURL}")` }} 
                            ></span>
                        </div>
                        
                    </div>
                    {
                        !song.isPlaying(track)
                        ?
                        <div className = "play-btn" onClick = {()=>song.playSong(track)}>
                        <span className = "material-icons">
                            play_arrow
                        </span>
                        </div>
                    :
                    <div className = "play-btn" onClick = {()=>song.stop()}>
                    <span className = "material-icons">
                        pause
                    </span>
                    </div>
                    }
                        
                    <div className = "music-card-info-container">
                        <div className = "music-card-left">
                            <div className = "music-card-title">
                                <h5>{track.info.title}</h5>
                            </div>
                        </div>
                        <div className = "music-card-middle">
                            <div className = "music-card-uploader">
                                    <span>{track.uploader.info.name}</span>
                            </div>
                            <div className = "music-card-genre">
                                    <span>{track.info.genre}</span>
                            </div>
                            
                        </div>
                        
                        <div className = "music-card-bottom">
                            <div className = "music-card-buttons-container">
                                <div className = "music-card-share">
                                   
                                </div>
                                <div className = "music-card-views">
                                    
                                </div>
                                <div className = "music-card-comments">
                                    
                                </div>
                                <div className = "music-card-like">

                                </div>
                            </div>
                        </div>
                    </div>
                    <ThreeDots song = {track} />
                </div>
            </div>
        </div>
    )
}

export default MusicCard