import React, { useContext, useEffect, useState } from "react";
import { useLocation } from 'react-router-dom';
import Top from "./comp/top"
import Moosix from "../functions/mossix-api.js"
import "./styles/listen.css"
import {Helmet} from "react-helmet";
const app = new Moosix()

function Listen(props)
{
    let query = useQuery();
    const [musics, setMusics] = useState(null)
    function useQuery() {
        return new URLSearchParams(useLocation().search);
    }
    const music = query.get("m")
    const uid = query.get("uid")
    useEffect(()=>{
        if(query){
            app.getSongsById(uid, music).then(tracks=>{
                setMusics(tracks)
            })
        }
    }, [music])
    return(
        <div id = "listen" className = "main" >
            <Helmet>
                <title>{musics&&musics.info.title + " • Moosax"}</title>
            </Helmet>
            <div className = "music-content-container" id = "listen-music">
                <div className = "music-box" id = "listen-box">
                    <Top tracks = {musics} nowPlaying = {null} play ={props.play} audio = {props.audio} stop = {props.stop} top = "top-listen"/>
                </div>
            </div>
        </div>
    )
}
export default Listen