import React, { useEffect } from "react";
import {Helmet} from "react-helmet";
import FollowBtn from "./sub-com/followBtn"
import { useState } from "react"
import { Route } from "react-router-dom";
import {MoosaxAuth} from "./functions/moosax-auth.js"
import MusicContent from "./musicContent"
import { Switch } from 'react-router-dom';
import Moosix from "./functions/mossix-api"
const app = new Moosix()
const Music = (props) => {
    const auth = new MoosaxAuth()
    const [x, setX] = useState()
    const [y, setY] = useState()
    const [zoom, setZoom] = useState()
    const [user_data, setUser_data] = useState()
    const [musics, setMusics] = useState([])
    const [user, setUser] = useState(null)
    const [pfp, setPfp] = useState(null)
    const[bio, setBio] = useState(null)
    const [currentUserName, setCurrrentUserName] = useState()
    const { params: { uID } } = props.match;
    //profile pictures and all
  
    useEffect(() => {
        app.getUserData(uID).then((data)=>{
            setUser_data(data)
            setMusics(data.tracks)
        })
    }, [uID])
    useEffect(()=>{
            if(user_data){
                setBio(user_data.public.profile.info.bio)
                setCurrrentUserName(user_data.public.profile.info.name)
                setUser(user_data.public.profile.info.name)
                setPfp(user_data.public.profile.pfp.url)
                setX(user_data.public.profile.pfp.x)
                setY(user_data.public.profile.pfp.y)
                setZoom(user_data.public.profile.pfp.zoom)
            }
    }, [user_data])
    

    //stores musics data in array for rendering


    //play function no bbugs here
    function play(src) {
        console.log(props)
        props.play(src)
     }
  
    //no bugs here
   

    //no bugs here
    function stop() {
        props.stop()
    }
  
    
    return (
        <>
            <Helmet>
                <title>{user && user+" • Moosax"}</title>
            </Helmet>

            <div id="profile" className="main">
                <div className="top-profile-container">
                    <div className = "profile-info-container">
                        <div className="cover-pic" style = {{backgroundImage: `linear-gradient(to bottom, rgba(0, 0, 0, 0), var(--dark-secondary)), url("${pfp}")`}}>
                        
                        </div>
                        <div className = "user-head-container">
                            <div className = "user-info">
                                <div className = "user-info-header">
                                    <div className="user-name">
                                        <h1>{user}</h1>
                                    </div>
                                    <FollowBtn uid = {uID} following = {user_data?.isFollowing || false}/>
                                </div>
                               <div className = "user-info-middle">
                                    <div className = "user-bio">
                                        <span>{bio}</span>
                                    </div>
                               </div>
                               <div className = "user-info-footer">

                               </div>

                                
                            </div>  
                        </div>
                        
                       
                    </div>
                    <div className="pfp">
                            <div className = "pfp-container">
                                <pfp className = "pfp-box">
                                    <img 
                                    src={pfp}  
                                    style = 
                                    {{
                                        transform: `translate(${x-50}px, ${y-50}px) scale(${(parseInt(zoom)/100) + 1 })`,     
                                    }}
                                    />
                                </pfp>
                            </div>
                    </div>
                   
                </div>
                
                <Switch>
                    <Route exact path={"/u/" + uID + "/music"}>
                        <MusicContent
                            musics={musics}
                            play={play}
                            uID={uID}
                            userName={currentUserName}
                            pfp={pfp}
                            user={user}
                            stop = {stop}
                            audio = {props.audio}
                        />
                    </Route>
                    <Route exact path={"/u/" + uID + "/playlist"}>
                    </Route>
                </Switch>
            </div>
        </>
    )
}
export default Music



