import React, { useContext, useEffect, useState } from "react";
import {songContext} from "../context/songContext"
import {useComponentVisible} from "./functions/useComponentVisible"
import "../styles/threedots.css"

function ThreeDots(props)
{
    const {
        ref,
        isComponentVisible,
        setIsComponentVisible
    } = useComponentVisible(false);

    const song = useContext(songContext)
    const [show, setShow] = useState(false)
    const [qued, setQued] = useState(song.isQued(props.song))
    
    function que()
    {
        song.loadSongs(props.song)
        setQued(true)
    }
    function remove()
    {
        song.removeSongs(props.song)
        setQued(false)
    }

   
    return(
        <div id = "three-dot" >
            <div className = "three-dot-container"ref = {ref}  onClick = {()=>{
               setIsComponentVisible(!isComponentVisible)
                console.log(isComponentVisible)
                }}>
                <div className = "dot-icon">
                    <span class="material-icons" >
                        more_horiz
                    </span>
                </div>
                    {isComponentVisible && (
                    <Options song = {props.song} que = {que} remove = {remove} qued= {qued} 
                    />)}
            </div>
        </div>
    )
}

function Options(props)
{
    
    return(
        <div id = "dot-option">
            <div className = "dot-option-container">
                <div className = "dot-option-box">
                    <div className  = "dot-option-content">
                        {
                           props.qued?
                            <div className = "add-que" onClick = {()=>{
                                props.remove()
                               
                            }}>
                                <i class="material-icons">
                                    queue_music
                                </i> 
                                <span >remove que</span>
                            </div>
                            :
                            <div className = "add-que" onClick = {()=>{
                                props.que()
                            }}>
                                <i class="material-icons">
                                    queue_music
                                </i> 
                                <span >que</span>
                            </div>   
                        }
                        <div className = "share">
                            <i class="material-icons">
                                reply
                            </i>
                            <span>share</span>
                        </div>
                        <div className = "report">
                        <i class="material-icons">
                            flag
                        </i>
                            <span>report</span>
                        </div>
                        <div className = "cancel">
                            <i class="material-icons">
                                close
                            </i>
                            <span>cancel</span>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default ThreeDots