import React, { useState, useEffect, useContext } from "react"
import Moosix from "../functions/mossix-api.js"
import { uidContext } from "../../context/uid.js"
import NotficationContents from "./notification-contents"
import "../../styles/notification.css"
const app = new Moosix()
function Notification(props) {

    const uid = useContext(uidContext)
    const [notification, setNotification] = useState(null)
    useEffect(() => {
        app.getNotifications().then(notification => setNotification(notification))
    }, [uid])


    return (
        <div id="notification">
            <div className="notification-container">
                <div className="notification-box">
                    <div className="notfication-header">
                        <span>Notifications</span>
                    </div>
                    <div className="notification-contents">
                        {notification && notification.slice(0).reverse().map((data, index) => <NotficationContents key={index} notification={data} />
                        )}
                    </div>
                </div>
            </div>
        </div>
    )
}



export default Notification