import io from "socket.io-client"
import React, {useEffect, useState} from"react"

function Notification(id){
    const [socket, setSocket] = useState()
    useEffect(()=>{
        if(id){
            const newSocket = io({ 
                transport : ['websocket'], 
                query: {
                    "id": id
                }
            });
            setSocket(newSocket)
            return()=>newSocket.close()
        }
    }, [id])
    return socket
}
export default Notification