import React, {useState, useEffect} from "react";
import { NavLink } from "react-router-dom";
import {uidContext} from "../../context/uid"
import {MoosaxAuth} from "../functions/moosax-auth.js"
function NavDiv2()
{
    const auth = new MoosaxAuth()
    const [uid, setUid] = useState()
    useEffect(()=>{
        auth.getUserId().then((uid)=>{
            setUid(uid)
        })
    }, [])
    return(
            <div className = "nav-div2">
                <span className = "nav-heading">Library</span>
                <ul>
                    <NavLink to="/history" exact activeClassName="btn-active">
                        <li className="btn">
                            <span class="material-icons">
                                history
                            </span>
                            <p>history</p>
                        </li>
                    </NavLink>
                    <NavLink to="/liked" exact activeClassName="btn-active">
                        <li className="btn" >
                            <span class="material-icons">
                                favorite
                            </span>
                            <p>Liked</p>
                        </li>
                    </NavLink>
                    <NavLink to={"/u/"+uid+"/music"} exact activeClassName="btn-active">
                        <li className="btn" >
                            <span class="material-icons md-dark md-inactive">
                                album
                            </span>
                            <p>Your Music</p>
                        </li>
                    </NavLink>
                </ul>
            </div>
    )
}

export default NavDiv2