import firebase from "firebase"
import Cookies from "js-cookie"
class Moosix
{
    constructor(uid){
        this.uid = uid
    }

    async getPopular()
    {
        const res = await fetch("/api/home/popular", { mode: "cors" })
        const response = await res.json()
        return response
    }

    async getTrending()
    {
        const res = await fetch("/api/home/trending", { mode: "cors" })
        const response = await res.json()
        return response
        
    }

    async getRecommend(userId)
    {
        const res = await fetch("/api/home/recomended")
        const response = await res.json()
        return response
    }

    async getRecommendByFollowing()
    {
        const res = await fetch("/api/home/following")
        const response = await res.json()
        return response
    }

    async getUserData(userId)
    {
        const res = await fetch (`/api/u/${userId}`)
        const response = await res.json()
        return response
    }

    async getSongsById(userid, song_id)
    {
        const res = await fetch ("/api/listen?song="+song_id)
        const response = await res.json()
        return response
    }

  


    async createAcc(data)
    {
        const url = "/api/account/create"
        await this.postData(url, data)
        return "ok"
    }

    


    async searchQ(search)
    {
        if(search != "" && search != null)
        {
            const res = await fetch("/api/home/result?search="+search, {mode: 'cors'})
            const response = await res.json()
            return response
        }
    }

    async getNotifications(uid)
    {
        const res = await fetch("/api/u/notification", {mode: 'cors'})
        const response = await res.json()
        return response
        
    }

    checkName(name)
    {
        const url = "/api/profile/checkname"
        if(name != null || name != ""){
            return this.postData(url, {"name": name})
        }
    }

    async update_profile(data)
    {
        const st = firebase.storage()
        await st.ref("/users/profile/pfp").child(data.pic.name).put(data.pic)
                .then((snapshot)=>snapshot.ref.getDownloadURL())
                .then(url=>data.public.profile.pfp.url = url)
        const url = "/api/profile/update"
        await this.postData(url, {"update": data})
        return "ok"
    }

    async upload(data)
    {
        const rawData = data
        const st = firebase.storage()
        const url = "/api/music/upload"
        const imageTask =st.ref("public/"+"songs/"+data.genre+"/"+data.title).child(data.image.name).put(data.image)
        await imageTask.then(snapshot=>snapshot.ref.getDownloadURL()).then(url=>{
            console.log(url)
            rawData.imageURL = url
        })
        const songTask = st.ref("public/"+"songs/"+data.genre+"/"+data.title).child(data.file.name).put(data.file)
        await songTask.then(snapshot=>snapshot.ref.getDownloadURL()).then(url=>rawData.audioURL = url).then(()=> this.postData(url, rawData))
       
    }

    async getUserLikes(uid)
    {
        if(uid){
            const res = await fetch("/api/u/liked?uid="+uid)
            const response = await res.json()
            return response
        }else{
            const res = await fetch("/api/u/songs/liked")
            const response = await res.json()
            return response
        }
       
    }

    async update_profile_pic(pic)
    {
        const st = firebase.storage()
        await st.ref("/users/profile/pfp").child(pic.name).put(pic)
                .then((snapshot)=>snapshot.ref.getDownloadURL())
                .then(url=>this.url = url)
                .then(()=>console.log("completed"))
    }

    
    
    sendLike(liker_uid, u_uid, song_id, music)
    {
        const url = "/api/music/like/send"
        const data = 
        {
            likedOf: u_uid,
            songID: song_id,
            song: music
        }
        this.postData(url, data)
    }
    
    async follow(uid)
    {
        const url = "/api/u/follow"
        const data = {
            uid: uid
        }
        this.postData(url, data)
    }




    async  postData(url = '', data = {}) {
        // Default options are marked with *
        const response = await fetch(url, {
          method: 'POST', // *GET, POST, PUT, DELETE, etc.
          mode: 'cors', // no-cors, *cors, same-origin
          cache: 'no-cache', // *default, no-cache, reload, force-cache, only-if-cached
          //credentials: 'same-origin', // include, *same-origin, omit
          credentials: 'same-origin', // include, *same-origin, omit
          headers: {
            'Content-Type': 'application/json',
            "CSRF-Token": Cookies.get("XSRF-TOKEN")
          },
          redirect: 'follow', // manual, *follow, error
          referrerPolicy: 'no-referrer', // no-referrer, *no-referrer-when-downgrade, origin, origin-when-cross-origin, same-origin, strict-origin, strict-origin-when-cross-origin, unsafe-url
          body: JSON.stringify(data) // body data type must match "Content-Type" header
        });
        return response.json(); // parses JSON response into native JavaScript objects
    }
}
export default Moosix